import React from "react";
import {
  FormControl,
  Checkbox,
  FormControlLabel,
  withStyles
} from "@material-ui/core";

import NavButton from "../../components/nav-button/nav-button.component";

import "./hindrance.styles.scss";

const styles = theme => ({
  label: {
    fontSize: "1.375rem",
    padding: "12px 20px 12px 44px",
    margin: "0 0 20px -44px",
    border: "1px solid silver",
    borderRadius: 0,
    width: "100%"
  }
});

const HindrancePage = ({
  hindrances,
  onHindranceChange,
  nextPath = "",
  userDesire,
  history,
  classes
}) => {
  return (
    <div className="page-root">
      <h1>
        Er det noe som gjør det vanskelig å{" "}
        <span className="t-italic">{userDesire.textDefinite}?</span>
      </h1>
      <p>Velg de hindringene du mener passer.</p>

      <div className="form-column">
        <FormControl>
          {hindrances
            .filter(hindrance => !hindrance.disabled)
            .map(hindrance => {
              return (
                <FormControlLabel
                  classes={{ label: classes.label }}
                  key={hindrance.id}
                  label={hindrance.text}
                  control={
                    <Checkbox
                      style={{ margin: "0 0 16px 12px" }}
                      checked={hindrance.selected}
                      onChange={() => onHindranceChange(hindrance.id)}
                      value={hindrance.id}
                    />
                  }
                />
              );
            })}
        </FormControl>
      </div>
      <div className="button-row">
        <NavButton secondary onClick={() => history.goBack()}>
          Forrige
        </NavButton>
        <NavButton onClick={() => history.push(nextPath)}>
          Gå til neste steg
        </NavButton>
      </div>
    </div>
  );
};

export default withStyles(styles)(HindrancePage);
